.formBox {
  /* position: absolute;
  top: 50%;
  left: 50%; */

  width: 400px;
  padding: 40px;
  background: rgba(0, 0, 0, 0.5);
  box-sizing: border-box;
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0.6);
  border-radius: 10px;
}
/* .inputBox {
    position: relative;
} */
.labelText {
  margin: 0 0 30px;
  padding: 0;
  color: #fff;
  text-align: center;
}
.formLabel {
  /* position: absolute;
  top:0;
  left: 0; */
  padding: 10px 0;
  font-size: 16px;
  color: #fff;
  /* pointer-events: none; */
  transition: 0.5s;
}
.formInput {
  width: 100%;
  padding: 10px 0;
  font-size: 16px;
  color: #fff;
  margin-bottom: 30px;
  border: none;
  border-bottom: 1px solid #fff;
  outline: none;
  background: transparent;
  transition: all 0.5s ease;
}
.formInput:hover {
  border-bottom-color: #03e9f4;
}
/* .formInput:focus ~ label,
.formInput:valid ~ label {
  top: -20px;
  left: 0;
  color: #03e9f4;
  font-size: 12px;
} */
.addBtn {
  /* position: relative; */
  display: inline-block;
  padding: 10px 20px;
  color: #03e9f4;
  font-size: 16px;
  text-decoration: none;
  text-transform: uppercase;
  /* overflow: hidden; */
  transition: 0.5s;
  margin-top: 40px;
  letter-spacing: 4px;
  border-color: transparent;

  /* width: 100%; */
  /* height: 2px; */
  background: linear-gradient(90deg, transparent, #03e9f4);
  animation: btn-anim1 1s linear infinite;
}
.addBtn:hover {
  background: #03e9f4;
  color: #fff;
  border-radius: 5px;
  box-shadow: 0 0 5px #03e9f4, 0 0 25px #03e9f4, 0 0 50px #03e9f4,
    0 0 100px #03e9f4;

  cursor: pointer;
}
